import React from "react";
import { Marquee } from "@/components/magicui/marquee"; // ShadCN Marquee

// Replace these with your actual logo image paths
const logos = [
  "/img/Askaiilogo (3)-e0C_eUdN.jpeg",
  "/img/Askaiilogo (6)-C6kPm1DY.jpeg",
  "/img/Askaiilogo (8)-B3jViTEY.jpeg",
  "/img/Askaiilogo (9)-38fntK-V.jpeg",
  "/img/Askaiilogo (10)-D4D5YweH.jpeg",
  "/img/Askaiilogo (11)-lJNEYut-.jpeg",
  "/img/Askaiilogo (13)-D5In7Wjn.jpeg",
  "/img/Askaiilogo (14)-CAvEBtXj.jpeg",
  "/img/Askaiilogo (15)-q52WhvYB.jpeg",
  "/imgAsskai logo 1-BlCOVKVa.jpg",
];

function LogoMarquee() {
  const topRow = logos.slice(0, 5); // First 5 logos
  const bottomRow = logos.slice(5, 10); // Last 5 logos

  return (
    <div className="w-full py-10">
      <div className="max-w-7xl mx-auto space-y-6">
        {/* Top Row - moves left */}
        <Marquee className="gap-10">
          {topRow.map((src, index) => (
            <img
              key={index}
              src={src}
              alt={`Logo ${index + 1}`}
              className="h-16 w-auto object-contain"
            />
          ))}
        </Marquee>

        {/* Bottom Row - moves right */}
        <Marquee reverse className="gap-10">
          {bottomRow.map((src, index) => (
            <img
              key={index + 5}
              src={src}
              alt={`Logo ${index + 6}`}
              className="h-16 w-auto object-contain"
            />
          ))}
        </Marquee>
      </div>
    </div>
  );
}

export default LogoMarquee;
